 !important; /* Many screen reader and browser combinations announce broken words as they would appear visually. */\n\n    &:focus {\n        background-color: black;\n        border-radius: 3px;\n        box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);\n        clip: auto !important;\n        clip-path: none;\n        color: white;\n        display: block;\n        font-family: inherit;\n        font-size: 1.3em;\n        font-weight: bold;\n        height: auto;\n        left: 5px;\n        line-height: normal;\n        padding: 15px 23px 14px;\n        text-decoration: none;\n        top: 5px;\n        width: auto;\n        z-index: 100000;\n    }\n}\n\n.main-scroll {\n  flex: 1;\n  scroll-behavior: smooth;\n  overflow-y: auto;\n  max-height: 100vh;\n  height: 100%;\n  display: flex;\n  flex-direction: column;\n  overflow-x: hidden;\n}\n\n\n@import \"bulma/bulma\";\n\n// Add html and body styles to :host for webcomponents\n:host {\n  --primary: #{$wr-green};\n  --info: #{$wr-light-bg};\n  --warning: #{$wr-yellow};\n  --link: #{$wr-dark-blue};\n\n  //background-color: $body-background-color;\n  font-size: $body-size;\n  -moz-osx-font-smoothing: grayscale;\n  -webkit-font-smoothing: antialiased;\n  min-width: $body-min-width; // @todo(emma, 2023-11-06) investigate why this is here\n  text-rendering: $body-rendering;\n  text-size-adjust: 100%;\n\n  font-family: $body-family;\n  color: $body-color;\n  font-size: $body-font-size;\n  font-weight: $body-weight;\n  line-height: $body-line-height;\n\n  box-sizing: border-box;\n  \n}\n\n// Make available to screen readers while hiding element visually:\n.sr-only {\n  position: absolute;\n  width: 1px;\n  height: 1px;\n  padding: 0;\n  margin: -1px;\n  overflow: hidden;\n  clip: rect(0, 0, 0, 0);\n  white-space: nowrap;\n  border-width: 0;\n}\n","@charset \"utf-8\"\n/*! bulma.io v0.9.3 | MIT License | github.com/jgthms/bulma */\n@import \"sass/utilities/_all\"\n@import \"sass/base/_all\"\n@import \"sass/elements/_all\"\n@import \"sass/form/_all\"\n@import \"sass/components/_all\"\n@import \"sass/grid/_all\"\n@import \"sass/helpers/_all\"\n@import \"sass/layout/_all\"\n\n@import \"sass-embedded-legacy-load-done:0\"","/* Bulma Utilities */\n@charset \"utf-8\"\n\n@import \"initial-variables\"\n@import \"functions\"\n@import \"derived-variables\"\n@import \"mixins\"\n@import \"controls\"\n@import \"extends\"\n\n@import \"sass-embedded-legacy-load-done:2\"","@import \"mixins\"\n\n%control\n  +control\n\n%unselectable\n  +unselectable\n\n%arrow\n  +arrow\n\n%block\n  +block\n\n%delete\n  +delete\n\n%loader\n  +loader\n\n%overlay\n  +overlay\n\n%reset\n  +reset\n\n@import \"sass-embedded-legacy-load-done:15\"","@import \"derived-variables\"\n\n$control-radius: $radius !default\n$control-radius-small: $radius-small !default\n\n$control-border-width: 1px !default\n\n$control-height: 2.5em !default\n$control-line-height: 1.5 !default\n\n$control-padding-vertical: calc(0.5em - #{$control-border-width}) !default\n$control-padding-horizontal: calc(0.75em - #{$control-border-width}) !default\n\n=control\n  -moz-appearance: none\n  -webkit-appearance: none\n  align-items: center\n  border: $control-border-width solid transparent\n  border-radius: $control-radius\n  box-shadow: none\n  display: inline-flex\n  font-size: $size-normal\n  height: $control-height\n  justify-content: flex-start\n  line-height: $control-line-height\n  padding-bottom: $control-padding-vertical\n  padding-left: $control-padding-horizontal\n  padding-right: $control-padding-horizontal\n  padding-top: $control-padding-vertical\n  position: relative\n  vertical-align: top\n  // States\n  &:focus,\n  &.is-focused,\n  &:active,\n  &.is-active\n    outline: none\n  &[disabled],\n  fieldset[disabled] &\n    cursor: not-allowed\n\n// The controls sizes use mixins so they can be used at different breakpoints\n=control-small\n  border-radius: $control-radius-small\n  font-size: $size-small\n=control-medium\n  font-size: $size-medium\n=control-large\n  font-size: $size-large\n\n@import \"sass-embedded-legacy-load-done:13\"","// Colors\n\n$black:        hsl(0, 0%, 4%) !default\n$black-bis:    hsl(0, 0%, 7%) !default\n$black-ter:    hsl(0, 0%, 14%) !default\n\n$grey-darker:  hsl(0, 0%, 21%) !default\n$grey-dark:    hsl(0, 0%, 29%) !default\n$grey:         hsl(0, 0%, 48%) !default\n$grey-light:   hsl(0, 0%, 71%) !default\n$grey-lighter: hsl(0, 0%, 86%) !default\n$grey-lightest: hsl(0, 0%, 93%) !default\n\n$white-ter:    hsl(0, 0%, 96%) !default\n$white-bis:    hsl(0, 0%, 98%) !default\n$white:        hsl(0, 0%, 100%) !default\n\n$orange:       hsl(14,  100%, 53%) !default\n$yellow:       hsl(44,  100%, 77%) !default\n$green:        hsl(153, 53%,  53%) !default\n$turquoise:    hsl(171, 100%, 41%) !default\n$cyan:         hsl(207, 61%,  53%) !default\n$blue:         hsl(229, 53%,  53%) !default\n$purple:       hsl(271, 100%, 71%) !default\n$red:          hsl(348, 86%, 61%) !default\n\n// Typography\n\n$family-sans-serif: BlinkMacSystemFont, -apple-system, \"Segoe UI\", \"Roboto\", \"Oxygen\", \"Ubuntu\", \"Cantarell\", \"Fira Sans\", \"Droid Sans\", \"Helvetica Neue\", \"Helvetica\", \"Arial\", sans-serif !default\n$family-monospace: monospace !default\n$render-mode: optimizeLegibility !default\n\n$size-1: 3rem !default\n$size-2: 2.5rem !default\n$size-3: 2rem !default\n$size-4: 1.5rem !default\n$size-5: 1.25rem !default\n$size-6: 1rem !default\n$size-7: 0.75rem !default\n\n$weight-light: 300 !default\n$weight-normal: 400 !default\n$weight-medium: 500 !default\n$weight-semibold: 600 !default\n$weight-bold: 700 !default\n\n// Spacing\n\n$block-spacing: 1.5rem !default\n\n// Responsiveness\n\n// The container horizontal gap, which acts as the offset for breakpoints\n$gap: 32px !default\n// 960, 1152, and 1344 have been chosen because they are divisible by both 12 and 16\n$tablet: 769px !default\n// 960px container + 4rem\n$desktop: 960px + (2 * $gap) !default\n// 1152px container + 4rem\n$widescreen: 1152px + (2 * $gap) !default\n$widescreen-enabled: true !default\n// 1344px container + 4rem\n$fullhd: 1344px + (2 * $gap) !default\n$fullhd-enabled: true !default\n\n// Miscellaneous\n\n$easing: ease-out !default\n$radius-small: 2px !default\n$radius: 4px !default\n$radius-large: 6px !default\n$radius-rounded: 9999px !default\n$speed: 86ms !default\n\n// Flags\n\n$variable-columns: true !default\n$rtl: false !default\n\n@import \"sass-embedded-legacy-load-done:4\"","@import \"derived-variables\"\n\n=clearfix\n  &::after\n    clear: both\n    content: \" \"\n    display: table\n\n=center($width, $height: 0)\n  position: absolute\n  @if $height != 0\n    left: calc(50% - (#{$width} * 0.5))\n    top: calc(50% - (#{$height} * 0.5))\n  @else\n    left: calc(50% - (#{$width} * 0.5))\n    top: calc(50% - (#{$width} * 0.5))\n\n=fa($size, $dimensions)\n  display: inline-block\n  font-size: $size\n  height: $dimensions\n  line-height: $dimensions\n  text-align: center\n  vertical-align: top\n  width: $dimensions\n\n=hamburger($dimensions)\n  cursor: pointer\n  display: block\n  height: $dimensions\n  position: relative\n  width: $dimensions\n  span\n    background-color: currentColor\n    display: block\n    height: 1px\n    left: calc(50% - 8px)\n    position: absolute\n    transform-origin: center\n    transition-duration: $speed\n    transition-property: background-color, opacity, transform\n    transition-timing-function: $easing\n    width: 16px\n    &:nth-child(1)\n      top: calc(50% - 6px)\n    &:nth-child(2)\n      top: calc(50% - 1px)\n    &:nth-child(3)\n      top: calc(50% + 4px)\n  &:hover\n    background-color: bulmaRgba(black, 0.05)\n  // Modifers\n  &.is-active\n    span\n      &:nth-child(1)\n        transform: translateY(5px) rotate(45deg)\n      &:nth-child(2)\n        opacity: 0\n      &:nth-child(3)\n        transform: translateY(-5px) rotate(-45deg)\n\n=overflow-touch\n  -webkit-overflow-scrolling: touch\n\n=placeholder\n  $placeholders: ':-moz' ':-webkit-inpu